"use client";
import { AiFillPlusCircle } from "react-icons/ai";
import React, { type JSX } from "react";

interface ButtonProps {
  text: string;
  onClick: () => void;
}

export const Button = ({ text, onClick }: ButtonProps): JSX.Element => {
  return (
    <button
      className="w-full text-white h-full rounded-md text-base bg-blue-600  hover:bg-blue-400 flex gap-2 justify-center items-center  transition-all"
      onClick={onClick}
    >
      <AiFillPlusCircle size={23} />
      {text}
    </button>
  );
};
