import React, { useState } from "react";
import { Button } from "../button/page";
import { Modal } from "../modal/page";

export const TicketLayout = () => {
  const [isModalOpen, setIsModalOpen] = useState(false);

  return (
    <div className="px-10 py-6 flex flex-col">
      <div className="flex justify-between items-center">
        <h1 className="font-montserrat text-2xl font-semibold text-gray-800">
          Chamados
        </h1>
        <div className="w-32 h-12">
          <Button text="New Desk" onClick={() => setIsModalOpen(true)} />
        </div>
      </div>

      {/* Modal */}
      {isModalOpen && (
        <Modal onTicketCreated={() => {}} setIsModalOpen={setIsModalOpen} />
      )}
    </div>
  );
};
